<template>
	<div>
		<div class="nav-top">
			<div class="brand py-2 px-4 text-left" @click="$router.push({name: 'Home'})">
				<div class="img d-inline-block mt-md-0 mt-2">
					<img src="@/assets/img/logo.png" class="mr-3" width="46px" alt="">
				</div>
				<div class="name d-md-inline-block d-none">
					<p class="mb-0">SIDESCRIPT</p>
					<p class="mb-0">DEVELOPER</p>
				</div>
			</div>
		</div>
		<div class="nav-right"></div>
		<div class="line-top"></div>
		<div class="line-right"></div>
		<div class="toggleable" @click="nav.active = !nav.active, setNavbar()">
			<icon icon="stream"></icon>
		</div>
		<div class="nav-full-page" :class="{'hide': nav.active == false}">
			<div class="toggleable" @click="nav.active = !nav.active, setNavbar()">
				<icon icon="times"></icon>
			</div>
			<div class="container p-4">
				<b-row>
					<b-col md="6" class="py-md-5 my-md-4 position-relative">
						<img src="@/assets/img/illust/illust-navbar.png" id="illust-navbar" class="w-100 position-absolute" alt="Navbar Illustration">
					</b-col>
					<b-col md="6" class="py-5 my-4">
						<div class="menu" @click="moveContent('Home')" :class="{'active': $route.name == 'Home'}">
							<h1 class="menu-main">HOME</h1>
							<h1 class="menu-shadow">HOME</h1>
						</div>
						<div class="menu" @click="moveContent('Service')" :class="{'active': $route.name == 'Service'}">
							<h1 class="menu-main">SERVICE</h1>
							<h1 class="menu-shadow">SERVICE</h1>
						</div>
						<div class="menu" @click="moveContent('Work')" :class="{'active': $route.name == 'Work'}">
							<h1 class="menu-main">WORK</h1>
							<h1 class="menu-shadow">WORK</h1>
						</div>
						<div class="menu" @click="moveContent('Achievement')" :class="{'active': $route.name == 'Achievement'}">
							<h1 class="menu-main">ACHIEVEMENT</h1>
							<h1 class="menu-shadow">ACHIEVEMENT</h1>
						</div>
						<div class="menu" @click="moveContent('About')" :class="{'active': $route.name == 'About'}">
							<h1 class="menu-main">ABOUT US</h1>
							<h1 class="menu-shadow">ABOUT US</h1>
						</div>
						<div class="menu" @click="moveContent('Contact')" :class="{'active': $route.name == 'Contact'}">
							<h1 class="menu-main">CONTACT</h1>
							<h1 class="menu-shadow">CONTACT</h1>
						</div>
					</b-col>
				</b-row>
				<b-row>
					<b-col md="12" class="text-left">
						<div class="icon-soc">
							<icon :icon="['fab', 'facebook']"></icon>
						</div>
						<div class="icon-soc">
							<icon :icon="['fab', 'dribbble']"></icon>
						</div>
						<div class="icon-soc">
							<icon :icon="['fab', 'github']"></icon>
						</div>
						<div class="icon-soc">
							<icon :icon="['fab', 'linkedin']"></icon>
						</div>
						<div class="icon-soc">
							<icon :icon="['fab', 'twitter']"></icon>
						</div>
					</b-col>
				</b-row>
			</div>
		</div>
	</div>
</template>

<script>
export default {
	data() {
		return {
			nav: {
				active: false
			}
		}
	},
	methods: {
		setNavbar() {
			if(this.nav.active == true)
				$('body').addClass('overflow-hidden')
			else
				$('body').removeClass('overflow-hidden')
		},
		moveContent(name) {
			this.nav.active = false
			$('body').removeClass('overflow-hidden')
			this.$router.push({name: name})
		}
	}
}
</script>